/* styles.css */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600&display=swap');

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  background-color: #081013;
  color: #cecece;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Hero styles */

.hero {
  position: relative;
  min-height: 100vh;
  background-color: #081013;
  overflow: hidden;
}

.hero-printer {
  position: absolute;
  top: 0;
  right: 0;
  width: 50%;
  height: 100%;
  overflow: hidden;
  z-index: 1;
}

.printer-image {
  position: absolute;
  top: 0;
  right: 80px;
  width: 150%;
  height: 150%;
  object-fit: cover;
  mix-blend-mode: soft-light;
  opacity: 0.7;
  transform: scale(1.2);
}

.header-content {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 0;
}

.logo-container {
  display: flex;
  flex-direction: column;
}

.logo {
  height: 40px;
  width: auto;
}

.logo-text {
  color: #ffffff;
  font-size: 0.875rem;
  margin-top: 0.5rem;
}

.main-nav {
  display: flex;
  gap: 2rem;
}

.main-nav a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1rem;
  transition: opacity 0.3s;
}

.main-nav a:hover {
  opacity: 0.8;
}

.social-icons {
  display: flex;
  gap: 1rem;
}

.social-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  transition: opacity 0.3s;
}

.social-icon img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.social-icon:hover {
  opacity: 0.8;
}

.hero-content {
  position: relative;
  z-index: 2;
  padding-top: 4rem;
}

.hero-text {
  max-width: 600px;
  font-family: 'Montserrat', sans-serif;
}

.hero-text h1 {
  font-size: 35px;
  font-weight: 600;
  color: #ffffff;
  line-height: 1.2;
  margin-bottom: 2rem;
}

.hero-text p {
  font-size: 20px;
  color: #cecece;
  line-height: 1.2;
  margin-bottom: 2.1rem;
}

.order-button {
  background-color: #76ba4d;
  color: #000000;
  font-size: 1.125rem;
  font-weight: 500;
  padding: 1rem 2rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.order-button:hover {
  background-color: #68a744;
}

@media (max-width: 768px) { 
  .header-content {
    position: relative;
    justify-content: center;
  }

  .order-button {
    background-color: #76ba4d;
    color: #000000;
    font-size: 15px;
    font-weight: 500;
    padding: 1rem 2rem;
    border: none;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: background-color 0.3s;
  }

  .logo-container {
    position: absolute;
    left: 30px;
    top: 15px;
  }

  .social-icons {
    display: none;
  }

  .hero-printer {
    position: absolute;
    width: 200%;
    height: 150%;
    right: 0;
    overflow: hidden;
    z-index: 1;
  }

  .printer-image {
    position: absolute;
    object-fit: cover;
    mix-blend-mode: soft-light;
    opacity: 0.7;
    transform: scale(1.2);
    width: 100%;
    height: 100%;
    right: 83px; /* Корректируем позицию изображения */

  }

  .hero-text {
    text-align: center;
    max-width: 600px;
    font-family: 'Montserrat', sans-serif;
    margin: 0 auto;
  }

  .hero-text h1 {
    font-size: 25px;
    font-weight: 600;
    color: #ffffff;
    line-height: 1.2;
    margin-bottom: 2rem;
  }
  
  .hero-text p {
    font-size: 15px;
    color: #cecece;
    line-height: 1.2;
    margin-bottom: 2.1rem;
  }

  .hero-content {
    display: flex;
    justify-content: center;
  }

  .main-nav {
    display: none; /* Скрываем навигацию на мобильных */
  }

  
}

@media (min-width: 769px) {
  .hamburger-menu-right {
    display: none; /* Hidden on desktop */
  }
}

.hamburger-menu {
  display: none;
}

.desktop-only {
  display: inline;
}

.mobile-only {
  display: none;
}

@media (max-width: 768px) {
  .desktop-only {
    display: none;
  }

  .mobile-only {
    display: inline;
  }
}


/* Reviews styles */
.reviews {
  padding: 0;
  margin-top: -350px;
  position: relative;
  z-index: 3;
}

.reviews-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  padding: 0 15px;
  mask-image: linear-gradient(to right, transparent, black 5%, black 95%, transparent);
  -webkit-mask-image: linear-gradient(to right, transparent, black 5%, black 95%, transparent);
}

.reviews-container {
  display: flex;
  animation: scroll 10s linear infinite;
  padding: 20px 0;
}

@media (max-width: 768px) {
  .reviews {
    margin-top: -250px;
  }


}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

.review-card {
  flex-shrink: 0;
  width: 320px;
  margin: 0 16px;
  padding: 24px;
  background-color: rgba(70, 70, 70, 0.199);
  backdrop-filter: blur(8px);
  border-radius: 8px;
  transition: transform 0.3s;
}

.review-card:hover {
  transform: scale(1.05);
}

.stars {
  color: #76ba4d;
  margin-bottom: 16px;
}

.review-text {
  margin-bottom: 16px;
}

.review-author {
  color: #5c62b2;
}




/* Portfolio styles */
.portfolio {
  padding: 64px 0;
}

.portfolio h2 {
  font-size: 24px;
  color: #ffffff;
  margin-bottom: 32px;
}

.portfolio-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 32px;
}

.portfolio-item {
  position: relative;
  aspect-ratio: 1;
  overflow: hidden;
  border-radius: 8px;
}

.portfolio-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.portfolio-overlay {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s;
}

.portfolio-item:hover .portfolio-overlay {
  opacity: 1;
}

.portfolio-overlay h3 {
  color: #ffffff;
  font-size: 20px;
}

/* Services styles */
/* Services styles */
.services {
  padding: 64px 0;
}

.services h2 {
  font-size: 24px;
  color: #ffffff;
  margin-bottom: 32px;
}

.services-scroll {
  overflow: hidden;
  position: relative;
  width: 100%;
  mask-image: linear-gradient(to right, transparent, black 5%, black 95%, transparent);
  -webkit-mask-image: linear-gradient(to right, transparent, black 5%, black 95%, transparent);
}

.services-track {
  display: flex;
  animation: scroll 10s linear infinite;
  gap: 16px;
}

@keyframes scroll {
  0% {
    transform: translateX(-50%);
  }
  100% {
    transform: translateX(0);
  }
}

.service-item {
  flex: 0 0 250px;
  background-color: #15181d;
  border-radius: 8px;
  padding: 24px;
  cursor: pointer;
  transition: background-color 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 12px;
}

.service-item:hover {
  background-color: rgba(21, 24, 29, 0.8);
}

.service-icon {
  color: #76ba4d;
  margin-bottom: 8px;
}

.service-item h3 {
  color: #ffffff;
  font-size: 16px;
  margin: 0;
}

.service-price {
  color: #76ba4d;
  font-size: 14px;
  margin: 0;
}

@media (max-width: 768px) {
  .service-item {
    flex: 0 0 200px;
  }
}



/* Footer styles */
.footer {
  background-color: #081013;
  padding: 64px 0;
  position: relative;
}

.footer-container {
  display: flex;
  justify-content: flex-end;
}

.footer-logo {
  position: absolute;
  left: 350px;
  bottom: 50px; /* Регулируйте это значение для поднятия логотипа выше */
}

.footer-logo img {
  height: 94px; /* Увеличил размер логотипа */
  width: 97px;
}

.footer-content {
  width: 75%; /* Занимает правую часть контейнера */
}

.footer-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 32px;
}

.location {
  font-weight: 600;
}

.address {
  color: #ffffff;
  margin-bottom: 8px;
}

.phone {
  margin-bottom: 16px;
}

.social-links {
  display: flex;
  gap: 16px;
}

.social-link {
  text-decoration: none;
  transition: opacity 0.3s;
}

.social-link:hover {
  opacity: 0.8;
}

.social-icon {
  width: 24px;
  height: 24px;
  object-fit: contain;
}



@media (max-width: 768px) {
  .footer-container {
    flex-direction: column;
  }

  .footer-logo {
    position: static;
    margin-bottom: 32px;
  }

  .footer-content {
    width: 100%;
  }

  .footer-grid {
    grid-template-columns: 1fr;
  }
}

